import React from 'react';
import { alpha, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import Icon from '@material-ui/core/Icon';
import SaveIcon from '@material-ui/icons/Save';
import Button from '@material-ui/core/Button';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MutiSelect from './MutiSelect'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { Grid } from '@material-ui/core';
import { Card } from '@material-ui/core';
import { MuiThemeProvider, useTheme } from '@material-ui/core';
import { TablePagination } from '@material-ui/core';
import MUIDataTable from 'mui-datatables';
import axios from "axios";
import routes from "../../routes/routes";
import { useState, useEffect } from "react";




const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },


}));


const ProductList = (props) => {
  const [open, setOpen] = useState(false);
  const [warehouses, setWarehouses] = useState([]);
  const [owarehouses, setOWarehouses] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [userId, setUserId] = useState("");
  const [refresh, setRefresh] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = React.useState(false);

  const handleChangePage = (event, newPage) => {
    console.log(newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    // console.log(parseInt(event.target.value, 10))
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleModalOpen = () => setModalOpen(true);
  const handleModalClose = () => setModalOpen(false);

  const EditClick = (event, _id, tableMeta) => {
    const data = warehouses.at(tableMeta.rowIndex);
    // getting the orignal item.....
    const rowdata = owarehouses.find((item) => item._id === data._id);
    console.log(rowdata);
    setAnchorEl(event.currentTarget);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen()
  };

  const columns = [
    {
      name: 'id',
      label: 'ID',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
      name: 'productName',
      label: 'Product Name',
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`
      }
    },
    {
        name: 'productCategory',
        label: 'Product Category',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'sku',
        label: 'SKU',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'barcode',
        label: 'Barcode',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'cost',
        label: 'Cost',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
      {
        name: 'quantity',
        label: 'Quantity',
        options: {
          filter: true,
          sort: true,
          customBodyRender: (value, tableMeta, updateValue) => `${value}`
        }
      },
  ];

  const option = {
    filterType: 'dropdown',
    selectableRowsHideCheckboxes: true,
    pagination: false
  };

  let productData = [];

  const getAllProducts = async () => {
    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
    });

    const [product] = await axios.all([pData]);

    console.log("productdata", product);

    // if (product.data.success) {
      setTotalCount(product.data.totalDoc);

      product.data.doc.map((data) => {
        console.log("dddddddddddddddd", data);
        const newdata = { _id: data._id, id: data.ID, productName: data.ProductName, productCategory: data.ProductCategories, sku: data.SKU, barcode: data.Barcode, cost: data.CurrentSHMDefaultCost, quantity: data.CurrentSHMQuantity };
        productData = [...productData, newdata];
        return productData;
      });
      setWarehouses(productData);
      console.log(warehouses);
      // setOWarehouses(warehouses.data.warehouses);
    // }
  };

  useEffect(() => {
    getAllProducts();
  }, [refresh, page, rowsPerPage]);


  return (
    <div className="container">
      <div>
      <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider >
                <MUIDataTable
                  style={{ th: { backgroundColor: 'grey' } }}
                  //   title="Employees List"
                  data={warehouses}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
      </div>






    </div>
  );
}
export default ProductList;